*{
  font-family: sans-serif;
}
body{
  background-color: #F3D0D0;
}

.header{
  font-size: 45px;
  margin: 50px;
}

.flex{
  display: flex;
}
.flex.ai-c{
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex.ai-s{
  flex-direction: column;
  justify-content: center;
  align-items: start;
}

form{
  background: #FFFFFF;
  padding: 40px;
  gap: 30px;
}

.result{
  background-color: #FFFFFF;
  padding: 30px;
}

.container{
  flex-direction: column;
  gap: 30px;
  margin-left: auto;
  margin-right: auto;
  width: 388px;
}
.categories{
  position: absolute;
  right: 50px;
  background: rgba(238, 0, 0, 0.27);
  padding: 35px;
}
footer{
  background: white;
  width: 100%;
  padding: 30px;
  font-size: 23px;
  position: absolute;
  bottom: 0px;
}
label{
  display: flex;
  gap: 10px;
}
input{
  width: 50px;
}
.btn{
  width: 90px;
  background: #178ED3;
  border: none;
  color: #FFFFFF;
  padding: 8px;
  border-radius: 4px;
}
form > div{
  gap: 10px;
}

@media screen and (max-width:1150px) {
  .main-section{
    flex-direction: column;
    align-items: center;
  }
  .categories{
    position: relative;
    width: 388px;
    right: 0px;
    margin: 25px;
  }
  footer{
    position: relative;
  }
}